import React from "react";
import { useState } from "react";
import { useEffect } from "react";
import { Link, useLoaderData, useLocation } from "react-router-dom";
import { FetchData } from "../../Utils/ApiUtils";
// import img from '../../../public/Images/electronics.png';
import { mapCategory } from "./../../Utils/utils";
import "./Category.scss";

function Category() {
  const [categories, setCategories] = useState([]);
  // const _categories = useLoaderData()
  // console.log(dataLoaded);
  useEffect(() => {
    getCategories();
    // setCategories(_categories);
  }, []);
  console.log(categories);

  async function getCategories() {
    const resp = await FetchData(
      "https://fakestoreapi.com/products/categories",
      "GET"
    ).then(async (resp) => {
      if (resp.status === 200) {
        const mappedCategories = mapCategory(resp.data);
        return await mappedCategories;
      } else {
        console.warn("sorry, this API failed");
        //// @TODO: we will handle it later// to work later
      }
    }).then((mappedCategories)=>{
      setCategories(mappedCategories);
    });
  }
  return (
    <div className="product-categories">
      {categories?.map((category, idx) => {
        return (
          <div key={idx}>
            <Link to={`/product?category=${category.name}`}>
              <div className={"card container my-5"}>
                <img src={category.image} alt={category.name} />
                {/* <img src={img} alt={category.name} /> */}

                <div
                  className={`card-img-overlay 
            custom-img-overlay
            d-flex justify-content-center 
            align-items-center`}
                >
                  {category.label}
                </div>
              </div>
            </Link>
          </div>
        );
      })}
    </div>
  );
}

export default Category;
